<template>
	<div class="modification-container">
		<div class="modification-header">
			<div class="modification-zhong">
				<div class="modification-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="modification-ti">修改绑定手机号</div>
				<div></div>
			</div>
		</div>
		<div class="modification-content">
			<div class="login-baise">
				<div class="modification-flex">
					<div class="modification-shouji">
						<img src="../assets/icon_shouji.png">
					</div>
					<div class="modification-haoma">
						<!--<input placeholder="请输入原手机号" type="text">-->
						<input placeholder="请输入原手机号" id="oldphone" type="" name="oldphone" v-model="oldphone">
					</div>
				</div>
				<div class="modification-border-b"></div>
				<div class="modification-between">
					<div class="modification-flex">
						<div class="modification-huoqu">
							<img src="../assets/icon_mima.png">
						</div>
						<div class="modification-yzm">
							<!--<input placeholder="请输入验证码" type="text">-->
							<input type="" placeholder="请输入验证码" v-model="oldcode">
						</div>
					</div>
					<!--<div>
					<div class="modification-yanzheng">获取验证码</div>
				</div>-->
					<div>
						<div class="getCode">
							<span v-show="show_old" @click="get_yzm_old">验证码</span>
							<span v-show="!show_old" class="count">{{count_old}}s后重新获取</span>
						</div>
					</div>
				</div>

				<div class="modification-border-b"></div>
				<div class="modification-flex">
					<div class="modification-shouji">
						<img src="../assets/icon_shouji.png">
					</div>
					<div class="modification-haoma">
						<!--<input placeholder="请输入新手机号" type="text">-->
						<input placeholder="请输入新手机号" id="newphone" type="" name="newphone" v-model="newphone">
					</div>
				</div>

				<div class="modification-border-b"></div>
				<div class="modification-between">
					<div class="modification-flex">
						<div class="modification-huoqu">
							<img src="../assets/icon_mima.png">
						</div>
						<div class="modification-yzm">
							<!--<input placeholder="请输入验证码" type="text">-->
							<input type="" name="pwd" placeholder="请输入新验证码" v-model="code">
						</div>
					</div>
					<!--<div>
					<div class="modification-yanzheng">获取验证码</div>
				</div>-->
					<div>
						<div class="getCode">
							<span v-show="show" @click="get_yzm">验证码</span>
							<span v-show="!show" class="count">{{count}}s后重新获取</span>
						</div>
					</div>
				</div>

			</div>

			<div class="modification-border-b"></div>
			<div class="modification-queren" @click="updt_phone()">确认</div>
		</div>
		<div class="modification-footer">

		</div>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				phone: '',
				code: '',
				oldphone: '',
				count: '',
				newphone: '',
				show: true,
				timer: null,
				yzm_params: {
					code_message: '获取验证码',
					is_send: true,
					time: 60,
					interval: ''
				},
				user_info: {},
				oldcode: '',
				count_old: '',
				show_old: true,
				timer_old: null,
				yzm_params_old: {
					code_message: '获取验证码',
					is_send: true,
					time: 60,
					interval: ''
				}
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			get_yzm() {
				var that = this;
				var flg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
				var asd = flg.test(that.newphone)
				if(!asd) {
					alert("请输入正确的手机号");
					return;
				}
				var obj = {
					phone: that.newphone
				};

				this.getCode()
				that.$http.post('/user/sendcode', Qs.stringify(obj), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					console.log(res)
					that.getCode();
				})
			},
			getCode() {
				const TIME_COUNT = 60;
				if(!this.timer) {
					this.count = TIME_COUNT;
					this.show = false;
					this.timer = setInterval(() => {
						if(this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},
			get_yzm_old() {
				var that = this;
				var flg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
				var asd = flg.test(that.oldphone)
				if(!asd) {
					alert("请输入正确的手机号");
					return;
				}
				var obj = {
					phone: that.oldphone
				};

				this.getCodeOld()
				that.$http.post('/user/sendcode', Qs.stringify(obj), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					console.log(res)
					that.getCodeOld();
				})
			},
			getCodeOld() {
				const TIME_COUNT = 60;
				if(!this.timer_old) {
					this.count_old = TIME_COUNT;
					this.show_old = false;
					this.timer_old = setInterval(() => {
						if(this.count_old > 0 && this.count_old <= TIME_COUNT) {
							this.count_old--;
						} else {
							this.show_old = true;
							clearInterval(this.timer_old);
							this.timer_old = null;
						}
					}, 1000)
				}
			},
			updt_phone() {
				var that = this;
				var flg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
				var asd = flg.test(that.newphone)
				if(!asd) {
					that.$alert("请输入正确的手机号", '提示');
					return;
				}
				if(that.code == "") {
					// alert("请输入验证码");
					that.$alert('请输入验证码', '提示');
					return;
				}
				var params = {
					userid: that.user_info.user_id,
					oldphone: that.oldphone,
					newphone: that.newphone,
					newcode: that.code,
					oldcode: that.oldcode
				};
				that.$http.post('/user/updt_phone', Qs.stringify(params), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					console.log(res);
					if(res.meta.code == 200) {
						window.localStorage.setItem('user_info', JSON.stringify(res.data));

						that.$router.push('/');
					} else {
						that.$alert(res.meta.msg, '提示');
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.modification-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
		background: url('../assets/beijing.png');
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
	
	.modification-header {
		height: 50px;
		/*background: #FFFFFF;*/
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.modification-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.modification-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.modification-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.modification-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.login-baise{
		background: hsla(0,0%,100%,.7);
		width: 90%;
		margin: 0 auto;
		margin-top: 2rem;
	}
	.modification-flex {
		display: flex;
	}
	
	.modification-shouji img {
		width: auto;
		height: 2rem;
		margin: 2.5rem 3.2rem 1.1rem 4.6rem;
	}
	
	.modification-haoma input {
		border: none;
		outline: none;
		background-color: transparent;
		font-size: 1.5rem;
		color: #adadad;
		/*padding-left: 1rem;*/
		margin-top: 2.5rem;
	}
	
	.modification-border-b {
		border-bottom: 1px solid #EAEAEA;
		margin: 0 3.9rem;
	}
	
	.modification-between {
		display: flex;
		justify-content: space-between;
	}
	
	.modification-huoqu img {
		width: auto;
		height: 2rem;
		margin: 3.3rem 1.5rem 1.1rem 4.6rem;
	}
	
	.modification-yzm {
		width: 6rem;
	}
	
	.modification-yzm input {
		border: none;
		outline: none;
		background-color: transparent;
		font-size: 1.5rem;
		color: #adadad;
		/*padding-left: 1rem;*/
		margin-top: 3.3rem;
	}
	
	.getCode {
		font-size: 1.2rem;
		color: #fd5997;
		border: 1px solid #fd5997;
		border-radius: 0.5rem;
		padding: 0.1rem 0.8rem;
		margin-top: 3.3rem;
		margin-right: 3.5rem;
	}
	
	.modification-queren {
		font-size: 1.8rem;
		color: #FFFFFF;
		border: 1px solid #fd5997;
		border-radius: 25px;
		background: #fd5997;
		text-align: center;
		padding: 0.5rem 0;
		width: 90%;
		margin: 3.5rem auto;
	}
</style>


